export const PersonalizedMessageBox = (props) => {
    const {setPreNameMessage, setPostNameMessage, preNameMessage, postNameMessage} = props;
    const returnText = preNameMessage + "Justin" + postNameMessage
    const testText = `Hello\nWorld\nXD`
    return (
        <div className = "PresonalizedMessageInput">
            <div className = "PreNameText">
                <label> Text Before Name:</label>
                <textarea onChange = {(event) => setPreNameMessage(event.target.value)}/>
            </div>
            <div className = "PostNameText">
                <label> Text After Name:</label>
                <textarea onChange = {(event) => setPostNameMessage(event.target.value)}/>
            </div>
            <div className = "TextPreview">
                <label> Text Preview:</label>
                <textarea value= {returnText} disabled={true} />
            </div>
        </div>
    )
}